import ProductItem from "@/app/component/ProductItem";
import { getDetail } from '../../services/ProductService'; 
import style from './id.module.css';
export default async function Home({ params }: { params: { id: string } }) {
    const { id } = params;
    const product = await getDetail(`http://localhost:3450/product/${id}`);

    return (
        <div className={style.productPage}>
            <div className={style.productContainer}>
                <div className={style.productImage}>
                    <img src={product.image} alt={product.name} />
                </div>
                <div className={style.productDetails}>
                    <h1 className={style.productTitle}>{product.name}</h1>
                    <p className={style.productPrice}>{product.price}₫</p>
                    <div>
                        <h1>Product</h1>
                        <ProductItem product={Product} />
                    </div>
                    <p className={style.productDescription}>{product.description}</p>
                    <div className={style.productActions}>
                        <button className={`${style.button} ${style.addToCart}`}>Thêm vào giỏ</button>
                        <button className={`${style.button} ${style.buyNow}`}>Mua ngay</button>
                    </div>
                </div>
            </div>
        </div>
    );
}
